.main-navigation{
    position:fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 3.5rem;
    background: teal;
    padding: 0 1rem;
    display: flex;
    align-items: center;
}

.main-navigation__logo h1{
    margin:0;
    font-size: 1.5rem;
}

.main-navigation__items{
    margin-left: 1.5rem;
    
}

.main-navigation__items ul{
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
}

.main-navigation__items li{
    margin: 0 1rem;
}

.main-navigation__items a{
    text-decoration: none;
    color: #fff
}

.main-navigation__items a:hover,
.main-navigation__items a:active,
.main-navigation__items a.active{
    color: rgb(189, 189, 69)
}
